<template>
    <div class="ma-3">
        <v-container class="container">
            <!-- Actions  -->
            <v-layout row class="mt-2 mb-3">
                <div class="text-center" v-if="stageProject">
                    <!-- Backward  -->
                    <v-btn
                        text
                        color="grey--text"
                        @click="previousProject"
                    >
                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>

                    <!-- Current  -->
                    <v-btn
                        outlined
                        class="mx-3"
                        v-if="stageProject"
                    >
                        {{ stageProject.title }}
                    </v-btn>
                    <v-btn
                        outlined
                        class="mx-3"
                        v-else
                    >Current Project</v-btn>
                    <!-- Forward  -->
                    <v-btn
                        text
                        color="grey--text"
                        @click="nextProject"
                    >
                        <v-icon
                            white
                        >mdi-arrow-right</v-icon>
                    </v-btn>
                </div>
            </v-layout>

            <!-- Displaying messages  -->
            <DisplayMessages />

            <!-- Project's form component  -->
            <Form :project="stageProject"/>
        </v-container>
    </div>
</template>

<script>
import axios from "axios";
import DisplayMessages from '@/components/DisplayMessages';
import Form from '../../components/Forms/project_detail';
export default {
    components: { DisplayMessages, Form },
    props: ['id'],
    data() {
        return {
            stageProject: {}
        }
    },
    mounted() {
        fetch('http://localhost:3000/project/' + this.id )
            .then(  res     => res.json())
            .then(  data    => this.stageProject = data)
            .catch( err     => console.log(err.message))
    },
    methods: {
        nextProject() {
            axios.get('http://localhost:3000/project/' + this.id + '/next')
            .then(res => {
                this.fetchData(res.data);
                this.$router.push('/project/' + res.data);
            })
        },
        previousProject() {
            axios.get('http://localhost:3000/project/' + this.id + '/previous')
            .then(res => {
                this.fetchData(res.data);
                this.$router.push('/project/' + res.data);
            })
        },
        fetchData(id) {
            this.project = '';
            fetch('http://localhost:3000/project/' + id)
                .then(  res     => res.json())
                .then(  data    => this.stageProject = data)
                .catch( err     => console.log(err.message))
        }
    }
}
</script>