<template>
    <v-container>
        <form ref="form" @submit.prevent="handleSubmit" >
            <!-- Detail Layout  -->
            <v-layout row wrap id="project-form">
                <v-col
                    md="7"
                    sm="12"
                >
                    <v-card class="py-5">
                        <v-card-text>
                             <v-text-field
                        v-model="project.title"
                        label="Project Name"
                        :counter="25"
                        outlined
                        clearable
                            ></v-text-field>
                            <v-select
                                v-model="project.person"
                                :items="staffs"
                                label="Person"
                                outlined
                                clearable
                            ></v-select>
                            <!-- Project start date  -->
                            <v-text-field
                                v-model="todayPicker"
                                label="Start Date"
                                outlined
                                disabled
                            ></v-text-field>
                            <v-menu
                                v-model="menu2"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                transition="scale-transition"
                                offset-y
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="project.due"
                                    label="Project due date"
                                    readonly
                                    outlined
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                                </template>
                                <v-date-picker
                                    v-model="project.due"
                                    @input="menu2 = false"
                                ></v-date-picker>
                            </v-menu>
                            <v-text-field
                                v-model="project.status"
                                label="Recent Comments"
                                :counter="17"
                                outlined
                                clearable
                            ></v-text-field>
                            <v-textarea
                                v-model="project.content"
                                outlined
                                name="input-7-4"
                                label="Project Description"
                                value="Random comment must be done here."
                            ></v-textarea>
                            <v-checkbox v-model="project_agree_term">
                                <template v-slot:label>
                                    <div>
                                    I agree the
                                    <v-tooltip bottom>
                                        <template v-slot:activator="{ on }">
                                        <a
                                            target="_blank"
                                            href="https://vuetifyjs.com"
                                            @click.stop
                                            v-on="on"
                                            style="text-decoration:none;"
                                        >
                                            Term & Condition
                                        </a>
                                        </template>
                                        Opens in new window
                                    </v-tooltip>
                                    to create this project
                                    </div>
                                </template>
                            </v-checkbox>
                        </v-card-text>
                        <v-card-actions>
                        <v-btn
                            class="mr-4"
                            color="primary"
                            @click="handleSave"
                        >Save changes</v-btn>
                        <v-btn
                            @click="clear"
                        >Clear</v-btn>
                    </v-card-actions>
                    </v-card>

                </v-col>

                <!-- Timeline Project  -->
                <v-col
                    md="5"
                    sm="12"
                >
                    <Timeline />
                </v-col>
            </v-layout>
        </form>
    </v-container>
</template>

<script>
// import axios from "axios";
import Timeline from '../Timeline'

export default {
    props: ['project'],
    components: { Timeline },
    data: () => ({
        staffs: [],
        menu2: false,
        todayPicker: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        project_agree_term: false,
        newProject: {},
        inputRules: [
            v => !!v || 'Required.',
            v => !!v || 'Name is required',
            v => v && v.length >= 4 && v.length <= 25 || 'Minimum length is 4 characters'
        ],
        inputRequired: [
            v => !!v || 'Required.'
        ],
        inputCmt: [
            v => v && v.length >= 10 || 'Minimum length is 10 characters'
        ]
    }),
    mounted() {
        fetch('http://localhost:3000/staff/list')
            .then(res => res.json())
            .then(data => this.staffs = data)
            .catch(err => console.log(err.message))
    },
    methods: {
        handleSubmit() {
            // this.$refs.form.validate();
            if(this.project_agree_term && !!this.newProject){
                console.log(this.newProject);
                // axios.post('http://localhost:3000/project/create', this.newProject)
                //     .then(res =>{
                //         console.log(res)
                //     })
                //     .catch(err => {
                //         console.log(err)
                //     })
            }else{
                console.log("Some condition are not met!")
            }
        },
        handleSave() {
            console.log("Saving the update.")
        },
        clear() {
            this.$refs.form.resetValidation()
            console.log("Reset btn is clicked.")
        }
    }
}
</script>